

main {
  margin: 0 auto;
  max-width: 100%;
  padding: 50px 30px;
  background-color: #deffff;
}
:root {
  --primary: #8c54ff;
  --secondary: #f3a847;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  background-color: #deffff;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--dark);
  color: var(--light);
  font-family: 'Poiret One', cursive;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: #ffffff;
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--dark);
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

span[role='img'] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav {
height: 100px;
margin: 0;
background-color: #F18081;
}

nav a:hover {
  text-decoration: none;
}

nav li {
  cursor: pointer;
}

textarea {
  width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
  align-items: right;
}

.myimg{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
  margin-left: 35px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  grid-template-areas: "c a b b";
  align-items: start;
}

.item1 {
  grid-area: a;
}
.item2 {
  grid-area: b;
  text-align: justify;
  text-justify: inter-word;
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
}

a {
  border-radius: 5px;
  color: var(--primary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
  text-decoration:none;  
}

.navActive {
  color: var(--secondary);
}

.social-icon {
  margin-right: 15px;
}

.flex-row {
  width: 100%;
}

.headers {
  display: inline;
  position: absolute;
  margin-top: 30px;
}

.nameEl {
  width: 30%;
  margin-left: 10px;
}

.navHeaders {
  width: 80%;
  position: absolute;
  right: 0;
  color: #020202;
  font-weight: bold;
}
.navUl {
  justify-content: space-between;
  position: absolute;
  right:0;
  top: 8px;
  display: flex;
  flex-wrap: wrap;
}
.navItem {
  margin-right: 35px;
  position: relative;
  right: 0;
  display: flex;
  flex-wrap: wrap;
  text-align: right;
  padding-left: 45px;
  font-size: large;
}

.con1:after {
  content: "";
  display: table;
  clear: both;
}

.appImage {
  float: left;
  display: inline;
  padding: 15px;
  background-color:#9fd2e4 ;
  margin: 50px;
}

.flex-container {
  display: flex;
  margin-top: 25px;
}

.flex-child {
  flex: 1;
  box-shadow: 0 4px 8px 0 #9fd2e4, 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
}  

.flex-child:first-child {
  margin-right: 50px;
} 
.flex-child:last-child {
  margin-right: 50px;
} 

.flex-child,ul,li{
  margin-bottom: 5px;
  list-style-type: square;
  padding-left: 5px;
}

footer,a:hover{
  transform: scale(1.3);
}

.navItem:hover {
  transform: scale(1.3);
}

.navItem:active {
  color: white;
}

.appImage:hover{
  transform: scale(1.3);
}

.selected{
  color: #b12704;
  transform: scale(1.3);
}

.notselected{
  color: #000000;
}